/* WordLetterCounter.css */
.container { 
	display: flex; 
	flex-direction: column; 
	align-items: center; 
	text-align: center; 
	font-family: Arial, sans-serif; 
	margin: 20px; 
} 
textarea { 
	width: 100%; 
	padding: 10px; 
	height: 300px; 
	margin-bottom: 10px; 
	border: 1px solid #fdc0c0; 
	border-radius: 5px; 
	font-size: 25px; 
} 

p { 
	font-size: 25px; 
	margin: 5px 0; 
	color: #372626; 
} 
